import React, { Component } from "react";
import axios from "axios";
import ReactMarkdown from "react-markdown";
import { Link } from "react-router-dom";
import readingTime from "reading-time";
import {
  HeartTwoTone,
  EyeOutlined,
  CommentOutlined,
  LikeOutlined,
  DislikeOutlined,
} from "@ant-design/icons";

class Posts extends Component {
  state = {
    fetchedData: [],
    time: "",
  };
  componentDidMount() {
    axios({
      method: "GET",
      url: "http://localhost:8080/post/",
      headers: { "Content-Type": "application/json" },
    }).then((res) => this.setState({ fetchedData: res.data, time: res.data }));
  }
  handleClick = (e) => {
    e.preventDefault();
    let id = [e.currentTarget.value];
    axios({
      method: "GET",
      url: `http://localhost:8080/post/${id}`,
      headers: { "Content-Type": "application/json" },
    }).then((resp) => console.log(resp));
  };
  render() {
    const { fetchedData } = this.state;

    return (
      <div className="container posts body">
        {fetchedData.map((post) => (
          <div className="box" key={post.id}>
            <div className="content">
              <img src="{https://picsum.photos/200/300}" alt="" />
              <h3>{post.title}</h3>
              <pre>{post.genre}</pre>
              <ReactMarkdown source={post.description} />
              <p>2 min read</p>
              <div  > 
                <Link to={`/post/${post.id}`}>
                  <button className="btn btn-sm btn-dark" value={post.id}>
                    Read more
                  </button>
                </Link>
              </div>
              <div style={{ display: "flex", justifyContent: "flex-end" }}>
                <ul className="mt-1"
                  style={{
                    display: "flex",
                    listStyle: "none",
                    justifyContent: "flex-start",
                  }}
                >
                  <li>
                    <EyeOutlined
                      style={{
                        fontSize: "15px",
                        width: "28px",
                        alignItems: "center",
                      }}
                      twoToneColor="#eb2f96"
                    />
                    <b aria-label="views">{post.views}</b>
                  </li>
                  <li >
                    <HeartTwoTone
                      style={{
                        fontSize: "15px",
                        width: "28px",
                        alignItems: "center",
                        cursor: "pointer",
                      }}
                      twoToneColor="#eb2f96"
                    />
                    <b>{post.likes}</b>
                  </li>
                  <li>
                    <CommentOutlined
                      style={{
                        fontSize: "15px",
                        width: "28px",
                        alignItems: "center",
                      }}
                      twoToneColor="#eb2f96"
                    />
                    <b>{post.totalComments}</b>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        ))}
      </div>
    );
  }
}

export default Posts;
