import React, { Component } from "react";
import axios from "axios";
import ReactMarkdown from "react-markdown";
import readingTime from "reading-time";
import {
  HeartTwoTone,
  EyeOutlined,
  CommentOutlined,
  LikeOutlined,
  DislikeOutlined,
} from "@ant-design/icons";

class Post extends Component {
  state = {
    fetchedData: [],
    comment: "",
    commentor: "",
    time: "",
  };

  componentDidMount() {
    axios({
      method: "GET",
      url: `http://localhost:8080/post/${this.props.match.params.id}`,
      headers: { "Content-Type": "application/json" },
    }).then((res) =>
      this.setState({
        fetchedData: res.data,
        time: readingTime(res.data.content).text,
      })
    );
  }
  handleLike = () => {
    axios({
      method: "GET",
      url: `http://localhost:8080/like/${this.props.match.params.id}`,
    });
  };

  upVote = (e) => {
    e.preventDefault();
    let id = [e.currentTarget.value];
    axios({
      method: "GET",
      url: `http://localhost:8080/comment+/${id}`,
    });

    console.log(id);
  };
  downVote = (e) => {
    e.preventDefault();
    let id = [e.currentTarget.value];
    axios({
      method: "GET",
      url: `http://localhost:8080/comment-/${id}`,
    });
  };
  writeComment = (e) => {
    e.preventDefault();
    this.setState({ [e.target.name]: e.target.value });
  };
  postComment = (e) => {
    e.preventDefault();
    let comment = {
      comment: this.state.comment,
      commentor: this.state.commentor,
    };
    console.log(comment);

    axios({
      method: "POST",
      url: `http://localhost:8080/comment/${this.props.match.params.id}`,
      headers: { "Content-Type": "application/json" },
      data: comment,
    });
  };

  render() {
    const { fetchedData } = this.state;
    return (
      <div className="container">
        <div className="row">
          {
            <div>
              <h1 className="mt-5">{fetchedData.title}</h1>
              <p>{this.state.time}</p>
              <ReactMarkdown source={fetchedData.content} />
              <div>
                <ul
                  style={{
                    display: "flex",
                    listStyle: "none",
                    justifyContent: "flex-end",
                  }}
                >
                  <li>
                    <EyeOutlined
                      style={{
                        fontSize: "28px",
                        width: "65px",
                        alignItems: "center",
                      }}
                      twoToneColor="#eb2f96"
                    />
                    <b>{fetchedData.views}</b>
                  </li>
                  <li onClick={this.handleLike}>
                    <HeartTwoTone
                      style={{
                        fontSize: "28px",
                        width: "65px",
                        alignItems: "center",
                        cursor: "pointer",
                      }}
                      twoToneColor="#eb2f96"
                    />
                    <b>{fetchedData.likes}</b>
                  </li>
                  <li>
                    <CommentOutlined
                      style={{
                        fontSize: "28px",
                        width: "65px",
                        alignItems: "center",
                      }}
                      twoToneColor="#eb2f96"
                    />
                    <b>{fetchedData.totalComments}</b>
                  </li>
                </ul>
              </div>
              <hr />
              <div className="row">
                <div className="write-new col-12">
                  <form onSubmit={this.postComment} method="post">
                    <input
                      name="commentor"
                      placeholder="Enter you name"
                      value={this.state.name}
                      onChange={this.writeComment}
                    />
                    <textarea
                      placeholder="Write your comment here"
                      name="comment"
                      value={this.state.comment}
                      onChange={this.writeComment}
                    />
                    <div>
                      <button type="submit">Submit</button>
                    </div>
                  </form>
                </div>
              </div>{" "}
              <br />
              <div className="commnets container">
                <div className="comment-body">
                  <ul className="comment-section">
                    {fetchedData.comments?.map((comment) => (
                      <ul key={comment.id} className="comment-section">
                        <li className="comment user-comment">
                          <div className="info" key={comment.id}>
                            <span>
                              <b>
                                <h6>{comment.commentor}</h6>
                              </b>
                            </span>
                            <span>
                              <em></em>
                              {comment.commentedOn}
                            </span>
                          </div>
                          <span className="avatar"></span>
                          <p>{comment.comment}</p>
                          <ul>
                            <li
                              className="btn btn-sm"
                              onClick={this.upVote}
                              value={comment.id}
                            >
                              <LikeOutlined />
                            </li>
                            <li style={{ listStyle: "none" }}>
                              <b>Votes {comment.votes}</b>
                            </li>
                            <li
                              className="btn btn-sm"
                              onClick={this.downVote}
                              value={comment.id}
                            >
                              <DislikeOutlined />
                            </li>
                          </ul>
                        </li>
                      </ul>
                    ))}
                  </ul>
                </div>
              </div>
            </div>
          }
        </div>
      </div>
    );
  }
}

export default Post;
